<template>
  <div>
    <div class="page-title">
      <h3>Отрядная статистика</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <p>Выберите отряд:</p>
      <select style="display:block" v-model="tag">
        <option v-for="s of squads" :key="s" >{{s}}</option>
      </select>
      <Loader v-if="loading"/>
      <table v-else>
        <tr>
          <th>Место</th>
          <th>Ник</th>
          <th>Кол-во игр</th>
          <th>Убийств</th>
          <th>Тимкиллов</th>
          <th>Смертей</th>
          <th>K/D</th>
          <th>Счет</th>
          </tr>
        <tr v-for="(i, index) of n" :key="index">
          <td>{{index + 1}}</td>
          <td><router-link :to="{name:'Player', params:{nick: n[index].playerName, stats: i}}">{{n[index].lastSquadPrefix + n[index].playerName}}</router-link></td>
          <td>{{n[index].totalPlayedGames}}</td>
          <td>{{n[index].kills}}</td>
          <td>{{n[index].teamkills}}</td>
          <td>{{n[index].deaths}}</td>
          <td>{{n[index].kdRatio}}</td>
          <td>{{n[index].totalScore}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    n: [],
    tag: "[FNX]",
    squads: [],
    loading: false,
  }),
  async mounted() {
    this.loading = true;
    let s = await fetch("stats.json");
    s = await s.json();
    for(let key in s.squadStatistics){
      this.squads.push(s.squadStatistics[key].prefix)
    }
    this.r();
  },
  watch: {
    tag: function (newt, old) {
      this.n = [];
      this.r();
    },
  },
  methods: {
    async refresh() {
      this.loading = true;
      this.currency = await this.r()
      this.loading = false;
    },
    async r() {
      this.loading = true;
      let r = await fetch("stats.json");
      r = await r.json();
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key].lastSquadPrefix ===  this.tag) {
          console.log(r.globalStatistics[key]);
          this.n.push(r.globalStatistics[key]);
        }
      }
      this.loading = false;
    },
  },
};
</script>
